rimary;\r\n    color: $white;\r\n\r\n    &:hover,\r\n    &:focus {\r\n        color: $white;\r\n        background-color: lighten($brand-primary, 5%);\r\n        border-color: lighten($brand-primary, 5%);\r\n    }\r\n\r\n    &:active {\r\n        color: $white;\r\n        background-color: darken($brand-primary, 5%);\r\n        border-color: darken($brand-primary, 5%);\r\n    }\r\n\r\n    &.active {\r\n        color: $white;\r\n        background-color: darken($brand-primary, 5%);\r\n        border-color: darken($brand-primary, 5%);\r\n\r\n        &:hover,\r\n        &:focus {\r\n            background-color: darken($brand-primary, 5%);\r\n            border-color: darken($brand-primary, 5%);\r\n        }\r\n    }\r\n\r\n    &:not([disabled]):not(.disabled):active,\r\n    &:not([disabled]):not(.disabled).active {\r\n        &:focus {\r\n            box-shadow: none;\r\n        }\r\n    }\r\n\r\n    &.ant-btn-background-ghost {\r\n        background-color: transparent;\r\n        border-color: $brand-primary;\r\n        color: $brand-primary;\r\n\r\n        &:hover,\r\n        &:focus,\r\n        &:active {\r\n            border-color: lighten($brand-primary, 5%);\r\n            color: lighten($brand-primary, 5%);\r\n        }\r\n    }\r\n\r\n    &.disabled,\r\n    &[disabled] {\r\n        color: rgba($body-color, 0.25);\r\n        background-color: $gray-lightest;\r\n        border-color: $border-color;\r\n\r\n        &.active,\r\n        &:active,\r\n        &:hover,\r\n        &:focus {\r\n            color: rgba($body-color, 0.25);\r\n            background-color: $gray-lightest;\r\n            border-color: $border-color;\r\n        }\r\n    }\r\n\r\n    &.ant-btn-clicked {\r\n        &:after {\r\n            border-color: $brand-primary;\r\n        }\r\n    }\r\n}\r\n\r\n.ant-btn-dangerous {\r\n    color: $brand-danger;\r\n    border-color: $brand-danger;\r\n\r\n    &:hover,\r\n    &:focus {\r\n        background-color: transparent;\r\n        color: lighten($brand-danger, 5%);\r\n        border-color: lighten($brand-danger, 5%);\r\n    }\r\n\r\n    &:active {\r\n        background-color: transparent;\r\n        color: darken($brand-danger, 5%);\r\n        border-color: darken($brand-danger, 5%);\r\n    }\r\n\r\n    &.ant-btn-primary {\r\n        background-color: $brand-danger;\r\n        border-color: $brand-danger;\r\n        color: $white;\r\n\r\n        &:hover,\r\n        &:focus {\r\n            color: $white;\r\n            background-color: lighten($brand-danger, 5%);\r\n            border-color: lighten($brand-danger, 5%);\r\n        }\r\n\r\n        &:active {\r\n            color: darken($brand-danger, 5%);\r\n            border-color: darken($brand-danger, 5%);\r\n        }\r\n    }\r\n\r\n    &.ant-btn-link {\r\n        color: $brand-danger;\r\n\r\n        &:hover,\r\n        &:focus {\r\n            background-color: transparent;\r\n            color: $brand-danger;\r\n        }\r\n\r\n        &:hover {\r\n            background-color: rgba($brand-danger, 0.1);\r\n        }\r\n\r\n        &:active {\r\n            background-color: rgba($brand-danger, 0.2);\r\n        }\r\n    }\r\n}\r\n\r\n.ant-btn-group {\r\n    .ant-btn-primary {\r\n        +.ant-btn-primary,\r\n        &:last-child:not(:first-child) {\r\n            border-left-color: lighten($brand-primary, 10%);\r\n        }\r\n    }\r\n}\r\n","ul {\r\n    padding-left: 0px;\r\n\r\n    &.icons-list {\r\n        margin: 10px 0;\r\n        list-style: none;\r\n        overflow: hidden;\r\n\r\n        li {\r\n            float: left;\r\n            width: 16.66%;\r\n            text-align: center;\r\n            list-style: none;\r\n            cursor: pointer;\r\n            height: 100px;\r\n            color: $body-color;\r\n            @include transition(color 0.3s ease-in-out); \r\n            position: relative;\r\n            margin: 10px 0;\r\n            border-radius: $border-radius;\r\n            overflow: hidden;\r\n            padding: 25px 0 0;\r\n\r\n            @include screen-tablet {\r\n                width: 33.33%;\r\n            } \r\n\r\n            @include screen-mobile {\r\n                width: 50%;\r\n            } \r\n            \r\n            > i {\r\n                font-size: 24px;\r\n                @include transition(transform 0.3s ease-in-out); \r\n            }\r\n\r\n            .icon-text {\r\n                display: block;\r\n                margin-top: 10px;\r\n            }\r\n\r\n            &:hover {\r\n                color: $brand-primary;\r\n                background-color: rgba($brand-primary, 0.1);\r\n\r\n                i {\r\n                    @include transform(scale(1.4));\r\n                }\r\n            }\r\n        }\r\n    }\r\n}","\r\n//--------------------------------------------------\r\n// [Responsive]\r\n//--------------------------------------------------\r\n\r\n// usage : @include screen-desktop {...} \r\n@mixin screen-desktop {\r\n    @media only screen and (min-width: 1440px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n// usage : @include screen-laptop-above {...} \r\n@mixin screen-laptop-above {\r\n    @media only screen and (min-width: 1440px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n// usage : @include screen-laptop {...} \r\n@mixin screen-laptop {\r\n    @media only screen and (max-width: 1440px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n// usage : @include screen-laptop-only {...} \r\n@mixin screen-laptop-only {\r\n    @media only screen and (min-width: 992px) and (max-width: 1440px)  {\r\n        @content;\r\n    }\r\n}\r\n\r\n// usage : @include screen-tablet-above {...} \r\n@mixin screen-tablet-above {\r\n    @media only screen and (min-width: 992px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n// usage : @include screen-tablet {...} \r\n@mixin screen-tablet {\r\n    @media only screen and (max-width: 992px) {\r\n        @content;\r\n    }\r\n}\r\n\r\n// usage : @include screen-mobile-above {...} \r\n@mixin screen-mobile-above 